@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
	height: 100%;
	display: flex;
	flex-direction: column;
	background-color: v.$gris-claro;

	&__header {
		background-color: v.$gris-oscuro;
		padding: 2rem;
	}
	&__header-grid {
		display: flex;
		flex-direction: column;
		@include m.tablet {
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
		}
	}
	&__logo {
		@include m.logo;
		@include m.texto-fluido(2rem, 3rem);
	}
	&__nav {

	}
	&__form {
		display: flex;
		justify-content: center;
	}
	&__submit--logout {
		@include m.enlace;
	}
	&__grid {
		display: flex;
		flex: 1;
	}
	&__sidebar {
		background-color: lighten($color: v.$gris-oscuro, $amount: 10%);
		width: 12rem;
	}
	&__menu {
		padding: 1rem;
		display: flex;
		flex-direction: column;
		gap: 1.5rem;
	}
	&__enlace {
		color: lighten($color: v.$gris-oscuro, $amount: 40);
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: .5rem;
		padding: 2rem;
		border-radius: 1rem;
		transition: 300ms background-color ease-in-out, 300ms color ease;
		&:hover {
			color: v.$blanco;
			background-color: lighten($color: v.$gris-oscuro, $amount: 20);
		}
	}
	&__icono {
		font-size: 2rem;
	}
	&__menu-texto {
		font-size: 1.1rem;
		font-weight: v.$bold;
	}
	&__contenido{
		padding: 1rem 1rem 3rem 1rem;
		width: 100%;
		@include m.tablet {
			padding: 1rem 5rem 3rem 5rem;
		}
	}
}